
.hiddenButton {

  font-size: 24px;
  color: #fff;
  transition: 0.5s;
  display: block;
  z-index: 999;
  position: fixed;
  right: 15px;
  top: 15px;
}



.main-screen {
background-color: teal;
height: 100vh;
right: 0px;


}

.parallax {
  background-color: tomato;
  
  /* The image used */
  background-image: url("../assets/hero-bg.jpg");
  box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.3);
  height: 100vh;

  /* Create the parallax scrolling effect */
  background-attachment: fixed; 
   background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

}

.parallax h1 , .parallax h3{
  color: #fff;
}




.screen{
  padding:0px
}

.navbar-section{

  background-color: #212121;
  height: 100vh;
  z-index: 999;
  top: 0px;
  left: 00px;
  bottom: 0px;
  position:fixed;
  transition: ease-in-out 0.5s;
  color: white;
}

@media ( max-width:768px ) {
  .navbar-section{
    left: -400px;  
  }
}

.narvar-section-hidden{
  left: -400px;
}

.navbar-section-show{
  left: 0px;
}

.navbar-section img {
  width: 120px;
  display: block;
  margin: auto;
  margin-top: 20px;
  border: 8px solid #1a1a20;
}



.two{
  float: right;
  position: relative;
  right:0px;
  width: 100%;
  height: 200vh;
  background-color: yellow;
  margin: 0px;
  padding:0px;
}

.main-cover{
  position:absolute;
  margin: 10%;

  
  width: auto;
  height: 200px;
}


/*----------------------LINKS---------------------------*/

